h1{
  color: orange;
  font-family: "Kalam", cursive;
  font-weight: 900;
  text-align: center;
  
}
.App{
  /* border: 2px solid black; */
  width: 320px;
  margin-top: 30px;
  
 padding: 10px;
 background-color: rgba(255, 255, 255, 0.2);
 border-radius: 20px;
 box-shadow: 0px 20px 40px rgb(171, 112, 1);
 backdrop-filter: blur(20px);
 -webkit-backdrop-filter: blur(20px);

}
#root{
  display: flex;
  align-items: center;
  justify-content: center;
}
.input_area{
  text-align: center;
  font-size: 30px;
height: 80px;
  width: 98%;
  /* margin-top: 20px; */
  margin-bottom: 20px;
  background-color: transparent;
border: none;
  border-radius: 50px;

  color: rgb(82, 53, 0);
}
.btn{
  border: 1px;
  font-size: 25px;
  font-weight: 500;
  margin: 5px;
height: 70px;
width: 70px;
border-radius: 50%;


}
.circle_con{
     height: 100vh;
     width: 100vw;
     position: absolute;
     /* background-color: antiquewhite; */
     z-index: -10000;
     display: flex;
     align-items: center;
     justify-content: center;
    
}
.circle{
  height: 320px;
  width: 320px;
  background-color:rgb(248, 197, 101);
  border-radius: 50%;
  margin: 10px;


}
.org{
  background-color: rgb(255, 166, 0);
}